* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.header-wrapper {
  z-index: 20;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
  margin: 24px 0;
  padding: 0 24px;
  color: #313131;
  font-weight: 200;
  font-size: 48px;
  background-color: white;
  -webkit-transition: font-size 0.2s;
  -o-transition: font-size 0.2s;
  transition: font-size 0.2s;
}

.header--stuck {
  font-size: 24px;
}

.header--has-shadow {
  -webkit-box-shadow: 0 2px 5px -2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 5px -2px rgba(0, 0, 0, 0.2);
}

.list {
  width: 500px;
  max-width: 100%;
  margin: auto;
}

.list-header-wrapper {
  top: 50px !important;
  z-index: 10;
  margin: 24px 0 0;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.list-header-wrapper--stuck {
  background-color: white;
  -webkit-box-shadow: 0 2px 5px -2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 5px -2px rgba(0, 0, 0, 0.2);
}

.list-header {
  width: 500px;
  max-width: 100%;
  margin: auto;
  padding: 6px 24px;
  color: #4c4c4c;
  font-weight: 200;
  font-size: 18px;
}

.list-item {
  width: 500px;
  max-width: calc(100% - 48px);
  margin: 12px 24px;
  padding: 12px;
  color: #2b2a2a;
  font-weight: 200;
  font-size: 20px;
  background-color: #f5f5f5;
  border-radius: 10px;
  -webkit-box-shadow: 1px 1px 5px -1px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 1px 5px -1px rgba(0, 0, 0, 0.2);
}

.avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  pointer-events: none;
}

.user {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.name {
  margin-left: 12px;
}



